	module.exports = {"data":{"postsJson":{"title":"More Teaching on True Discipleship","questionsTitle":"FOLLOWING GODLY EXAMPLES","display":"block","bookName":"Luke","ref":"16:1-17:10","html":"<p>   <strong>16</strong><u>1</u>He also said to his disciples,</p><h1>JESUS</h1><h2>There was a certain rich man who had a manager. An accusation was made to him that this man was wasting his possessions. <u>2</u>He called him, and said to him, ‘What is this that I hear about you? Give an accounting of your management, for you can no longer be manager.’<br />   <u>3</u>The manager said within himself, ‘What will I do, seeing that my lord is taking away the management position from me? I don’t have strength to dig. I am ashamed to beg. <u>4</u>I know what I will do, so that when I am removed from management, they may receive me into their houses.’ <u>5</u>Calling each one of his lord’s debtors to him, he said to the first, ‘How much do you owe to my lord?’ <u>6</u>He said, ‘A hundred batos* of oil.’ He said to him, ‘Take your bill, and sit down quickly and write fifty.’ <u>7</u>Then he said to another, ‘How much do you owe?’ He said, ‘A hundred cors* of wheat.’ He said to him, ‘Take your bill, and write eighty.’<br />   <u>8</u>His lord commended the dishonest manager because he had done wisely, for the children of this world are, in their own generation, wiser than the children of the light. <u>9</u>I tell you, make for yourselves friends by means of unrighteous mammon, so that when you fail, they may receive you into the eternal tents. <u>10</u>He who is faithful in a very little is faithful also in much. He who is dishonest in a very little is also dishonest in much. <u>11</u>If therefore you have not been faithful in the unrighteous mammon, who will commit to your trust the true riches? <u>12</u>If you have not been faithful in that which is another’s, who will give you that which is your own? <u>13</u>No servant can serve two masters, for either he will hate the one, and love the other; or else he will hold to one, and despise the other. You aren’t able to serve God and Mammon.*</h2><p>   <u>14</u>The Pharisees, who were lovers of money, also heard all these things, and they scoffed at him. <u>15</u>He said to them,</p><h1>JESUS</h1><h2>You are those who justify yourselves in the sight of men, but God knows your hearts. For that which is exalted among men is an abomination in the sight of God. <u>16</u>The law and the prophets were until John. From that time the Good News of God’s Kingdom is preached, and everyone is forcing his way into it. <u>17</u>But it is easier for heaven and earth to pass away, than for one tiny stroke of a pen in the law to fall. <u>18</u>Everyone who divorces his wife, and marries another, commits adultery. He who marries one who is divorced from a husband commits adultery.<br />   <u>19</u>Now there was a certain rich man, and he was clothed in purple and fine linen, living in luxury every day. <u>20</u>A certain beggar, named Lazarus, was laid at his gate, full of sores, <u>21</u>and desiring to be fed with the crumbs that fell from the rich man’s table. Yes, even the dogs came and licked his sores. <u>22</u>The beggar died, and he was carried away by the angels to Abraham’s bosom. The rich man also died, and was buried. <u>23</u>In Hades,* he lifted up his eyes, being in torment, and saw Abraham far off, and Lazarus at his bosom. <u>24</u>He cried and said, ‘Father Abraham, have mercy on me, and send Lazarus, that he may dip the tip of his finger in water, and cool my tongue! For I am in anguish in this flame.’<br />   <u>25</u>But Abraham said, ‘Son, remember that you, in your lifetime, received your good things, and Lazarus, in the same way, bad things. But now here he is comforted and you are in anguish. <u>26</u>Besides all this, between us and you there is a great gulf fixed, that those who want to pass from here to you are not able, and that no one may cross over from there to us.’<br />   <u>27</u>He said, ‘I ask you therefore, father, that you would send him to my father’s house; <u>28</u>for I have five brothers, that he may testify to them, so they won’t also come into this place of torment.’<br />   <u>29</u>But Abraham said to him, ‘They have Moses and the prophets. Let them listen to them.’<br />   <u>30</u>He said, ‘No, father Abraham, but if one goes to them from the dead, they will repent.’<br />   <u>31</u>He said to him, ‘If they don’t listen to Moses and the prophets, neither will they be persuaded if one rises from the dead.’</h2><p>   <strong>17</strong><u>1</u>He said to the disciples,</p><h1>JESUS</h1><h2>It is impossible that no occasions of stumbling should come, but woe to him through whom they come! <u>2</u>It would be better for him if a millstone were hung around his neck, and he were thrown into the sea, rather than that he should cause one of these little ones to stumble. <u>3</u>Be careful. If your brother sins against you, rebuke him. If he repents, forgive him. <u>4</u>If he sins against you seven times in the day, and seven times returns, saying, ‘I repent,’ you shall forgive him.</h2><p>   <u>5</u>The apostles said to the Lord,</p><h1>THE DISCIPLES</h1><h3>Increase our faith.</h3><p>   <u>6</u>The Lord said,</p><h1>JESUS</h1><h2>If you had faith like a grain of mustard seed, you would tell this sycamore tree, ‘Be uprooted, and be planted in the sea,’ and it would obey you. <u>7</u>But who is there among you, having a servant plowing or keeping sheep, that will say, when he comes in from the field, ‘Come immediately and sit down at the table,’ <u>8</u>and will not rather tell him, ‘Prepare my supper, clothe yourself properly, and serve me, while I eat and drink. Afterward you shall eat and drink’? <u>9</u>Does he thank that servant because he did the things that were commanded? I think not. <u>10</u>Even so you also, when you have done all the things that are commanded you, say, ‘We are unworthy servants. We have done our duty.’</h2><h5>*Luke 16:6 100 batos is about 395 liters or 104 U. S. gallons.<br />*Luke 16:7 100 cors = about 2,110 liters or 600 bushels.<br />*Luke 16:13 “Mammon” refers to riches or a false god of wealth.<br />*Luke 16:23 or, Hell</h5>","path":"/334","Question1":"In this passage is there an action that you should follow? What is it?","Question2":"What decisions can you make this week to do this?","Question3":"In this passage is there an action from which you should repent? What is it?","Question4":"What decisions can you make this week to do this?","prevPath":"/333","nextPath":"/335"}},"pathContext":{}}

